    @apply absolute top-1/2 left-1/2 w-auto h-auto object-contain transition-all duration-300 max-w-full max-h-full -translate-x-1/2 -translate-y-1/2;
  }
  .container-fluid {
    @apply px-4 md:px-6 lg:px-10 xl:px-20;
  }
