    @apply empty:hidden;
  }
  input {
    @apply outline-offset-0 shadow-none ring-0 !important;
  }
  .section {
    @apply py-10 lg:py-16 xl:py-20;
  }
  .section-40 {
    @apply py-6 lg:py-10;
  }
  .margin-p p + p {
