@keyframes leftBar {
  0% {
    height: 0;
    left: 0;
    top: 100%;
  }
  20% {
    height: 100%;
    left: 0;
    top: 0;
  }
  40% {
    height: 0;
    left: 0;
    top: 0;
  }
}
@keyframes topBar {
  0%, 20% {
    left: 0;
    top: 0;
    width: 0;
  }
  40% {
    left: 0;
    top: 0;
    width: 100%;
  }
  60% {
    left: calc(100% - 1px);
    top: 0;
    width: 0;
  }
}
@keyframes rightBar {
  0%, 40% {
    height: 0;
    left: calc(100% - 1px);
    top: 0;
  }
  60% {
    height: 100%;
    left: calc(100% - 1px);
    top: 0;
  }
  80% {
    height: 0;
    left: calc(100% - 1px);
    top: 100%;
  }
}
@keyframes bottomBar {
  0%, 60% {
    left: 100%;
    top: 100%;
    width: 0;
  }
  80% {
    left: 0;
    top: 100%;
    width: 100%;
  }
  100% {
    left: 0;
    top: 100%;
    width: 0;
  }
}
.header-hambuger.active span:nth-child(1) {
